@font-face {
    font-family: "Roboto Condensed";
    font-weight: 400;
    font-style: normal;
    font-display: swap;
    src: url("../fonts/Roboto_Condensed/Roboto_Condensed.woff2") format("woff2"),
      url("../fonts/Roboto_Condensed/Roboto_Condensed.woff") format("woff");
  }
  
  @font-face {
    font-family: "Roboto Condensed";
    font-weight: 300;
    font-style: normal;
    font-display: swap;
